import {getDetail} from '../../api/index'
let state={
   info:{},
   currentyear:'',
   
}

let getters={
     years(state){
         if(!state.info.list){
             return []
         }
         let arr=state.info.list.map(item=>{//arr
             return item.market_attribute.year
         })
         let set=new Set(arr)
         return ['',...set]//Es6Set,...
     },
     list(state){
        if(!state.info.list){
            return []
        }
        function mearg(arr){
           let types=[]
           let obj={}
           arr.forEach(item=>{
               let type=item.exhaust_str+'/'+item.max_power_str+''+item.inhale_type///+
               if(types.indexOf(type)==-1){//typestype
                   types.push(type)//types
                   obj[type]=[item]//type
               }else{
                   obj[type].push(item)//
               }
              
           })
           return obj
        }
        if(state.currentyear==''){//currentyear
            return mearg(state.info.list)//
        }else{
            return mearg(state.info.list.filter(item=>{
                return item.market_attribute.year==state.currentyear//currentyear
            }))
        }
        
     }
}

let mutations={
    changeyear:(state,payload)=>{
        state.currentyear=payload//currentyear
    },
    getDetailList:(state,payload)=>{
        payload.list.sort((a,b)=>{
            //  
            if(a.market_attribute.year!=b.market_attribute.year){
                return b.market_attribute.year-a.market_attribute.years
            }else{
                // 
                if(b.exhaust_str>a.exhaust_str){
                    return -1
                }else if(b.exhaust_str<a.exhaust_str){
                    return 1
                }else{
                    //  
                    if(b.max_power_str>a.max_power_str){
                        return -1
                    }else if(b.max_power_str<a.max_power_str){
                        return 1
                    }else{
                        //  
                            if(b.market_attribute.dealer_price_min>a.market_attribute.dealer_price_min){
                                return -1
                            }else{
                                return 1
                            }
                        }
                    }
                }
        })
        state.info=payload    
    }
}

let actions={
    getDetail:({commit},payload)=>{//id

        getDetail(payload).then(body=>{
            commit('getDetailList',body.data)
        })
    }
}

export default {
    namespaced:true,
    state,
    getters,
    mutations,
    actions 
}


